.App {
  text-align: center;
  font-family: "Catamaran", sans-serif;
}

.w3btn {
  background-color: #fd8f48;
  color: #ffffff;
  border: none;
  border-radius: 5%;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  margin: 10px 20px;
  cursor: pointer;
}
.w3btn:hover {
  color: #555555;
}

h1 {
  color: #444b54;
  font-weight: 700;
  font-size: 50px;
  text-align: center;
  line-height: 1.2;
}

.divhome {
  border: 4px solid #fd8f48;
  margin: 7vh 24vw;
}

.divload {
  display: flex;
  flex-direction: column;

  height: 50vh;
  width: 50vw;
}
.adminload {
  display: flex;
  flex-direction: column;

  height: 50vh;
  width: 50vw;
}
